import { Link } from "gatsby";
import routes from "../../../../constants/routes";
import React, { useEffect } from "react";
import { HorseListWrapper, HorseProfilePic, HorseUl } from "./styled";
import Placeholder from "../Placeholder";
import { ProfilePicWrapper } from "../../Misc/ProfilePicWrapper";
import { EditIcon } from "../../../../assets/icons/Edit";
import { patchSelectedHorse } from "../../../../redux/appState/action";
import { PlusIcon } from "../../../../assets/icons/PlusIcon";
import colors from "../../../../constants/colors";
import Logo from "../../Misc/Logo";
import { HorseHead } from "../../../../assets/icons/HorseHead";
import { Horse } from "../../../../redux/types";
import { useDispatch } from "react-redux";
import {
  SelectedHorse,
  setSelectedHorse,
} from "../../../../redux/appState/appStateSlice";

type HorseListProps = {
  horses: Horse[];
  show: boolean;
  openModal: () => void;
  isModalOpen: boolean;
  cms: any;
  patchSelectedHorse: (id: string) => void;
};

export default function HorseList({
  horses,
  show,
  openModal,
  isModalOpen,
  cms,
  patchSelectedHorse,
}: HorseListProps) {
  // reset selected Horse when this site is opened
  // useEffect(() => {
  //   if (typeof window !== "undefined") {
  //     patchSelectedHorse({ id: null, target: null, pos: null });
  //   }
  // }, []);

  const dispatch = useDispatch();

  const selectHorse = (selectedHorse: SelectedHorse) => {
    dispatch(setSelectedHorse({ selectedHorse }));
  };

  return (
    // @ts-ignore
    <HorseListWrapper show={show}>
      <Logo />
      <p style={{ margin: "0 auto" }}>{cms.Erklaerung}</p>
      <HorseUl>
        {horses.length ? (
          horses.map((horse: Horse, index) => {
            return (
              <li key={"pferde-" + index}>
                <Link
                  onClick={() => selectHorse({ id: horse.id })}
                  to={routes.detailView(horse.id)}
                  className={"link pferde-button"}
                >
                  <ProfilePicWrapper className={"large pferde-image"}>
                    {horse.profilePicture ? (
                      <img
                        src={horse.profilePicture}
                        alt={"Pferd Profilbild"}
                      />
                    ) : (
                      <HorseHead
                        fill={undefined}
                        stroke={undefined}
                        strokeLinecap={undefined}
                        strokeLinejoin={undefined}
                        strokeWidth={undefined}
                      />
                    )}
                  </ProfilePicWrapper>
                  <p className={"horse-name"}>{horse.name}</p>
                </Link>

                <button
                  className={"edit-button"}
                  onClick={() => {
                    console.log("selecting", horse.id);
                    selectHorse({ id: horse.id });
                    openModal();
                  }}
                >
                  <span>
                    <EditIcon
                      fill={undefined}
                      stroke={undefined}
                      strokeLinecap={undefined}
                      strokeLinejoin={undefined}
                      strokeWidth={undefined}
                      width={undefined}
                      height={undefined}
                    />{" "}
                    Edit
                  </span>
                </button>
              </li>
            );
          })
        ) : (
          <Placeholder openModal={openModal} cms={cms.Platzhalter} />
        )}
      </HorseUl>

      <button
        className={"add-horse"}
        style={{
          display: Object.keys(horses).length && !isModalOpen ? "flex" : "none",
        }}
        onClick={openModal}
      >
        <PlusIcon
          fill={colors.pineGreen["80"]}
          stroke={colors.pineGreen["80"]}
          strokeLinecap={undefined}
          strokeLineJoin={undefined}
          strokeWidth={undefined}
        />
      </button>
    </HorseListWrapper>
  );
}
