import React, { SyntheticEvent, useEffect, useState } from "react";
import { HorseHead } from "../../../../assets/icons/HorseHead";
import { Button } from "../../Misc/Button";
import { AddHorseForm } from "./styled";
import { ProfilePicWrapper } from "../../Misc/ProfilePicWrapper";
import { TrashIcon } from "../../../../assets/icons/Trash";
import * as Sentry from "@sentry/gatsby";
import { useSelector } from "react-redux";
import { RootState, useAppDispatch } from "../../../../redux/store";
import { Horse } from "../../../../redux/types";
import { setSelectedHorse } from "../../../../redux/appState/appStateSlice";
import _ from "lodash";
import {
  horseApi,
  useCreateHorseMutation,
  useDeleteHorseMutation,
  useHorseQuery,
  useUpdateHorseMutation,
} from "../../../../redux/api/horseApi";

type ModalProps = {
  closeModal: () => void;
  cms: any;
  isOpen: boolean;
  horses: Horse[];
};

export default function Modal({ closeModal, cms, isOpen }: ModalProps) {
  // global state
  const user = useSelector((state: RootState) => state.auth.user);
  const appState = useSelector((state: RootState) => state.appState);
  const selectedHorse = appState.selectedHorse;

  const dispatch = useAppDispatch();

  const {
    data: horseData,
    isFetching,
    isLoading,
  } = useHorseQuery(
    { userId: user?.id as string, horseId: selectedHorse.id },
    {
      refetchOnMountOrArgChange: true,
      skip: !user?.id || !selectedHorse?.id,
    }
  );

  const { refetch } = horseApi.endpoints.getHorse.useQuerySubscription(
    {
      userId: user?.id as string,
      horseId: selectedHorse?.id,
    },
    {
      refetchOnMountOrArgChange: true,
      skip: !user?.id || !selectedHorse?.id,
    }
  );

  type StateType = {
    name: string;
    profilePicture: string;
  };
  const initialState = { name: "", profilePicture: "" };

  const [state, setState] = useState<StateType>(horseData || initialState);
  const fileInput = React.createRef();
  const [fileInputChange, setFileInputChange] = useState(false);
  const [message, setMessage] = useState("");

  const [createHorse] = useCreateHorseMutation();

  const [updateHorse] = useUpdateHorseMutation();

  const [deleteHorse] = useDeleteHorseMutation();

  useEffect(() => {
    if (horseData && selectedHorse?.id && user?.id) {
      setState(_.pick(horseData, ["name", "profilePicture"]));
    } else {
      setState(initialState);
    }
  }, [horseData, isOpen]);

  useEffect(() => {
    if (isOpen && selectedHorse?.id && user?.id) {
      refetch();
    }
  }, [selectedHorse?.id, isOpen]);

  useEffect(() => {
    if (isOpen && selectedHorse?.id && user?.id) {
      setState(_.pick(horseData, ["name", "profilePicture"]));
    }
  }, [isLoading]);

  function onInputChange(event: SyntheticEvent) {
    event.persist();

    setState((prevState: StateType) => ({
      ...prevState,
      [event.target.name]: event.target.value,
    }));
  }

  function onSubmit(event: SyntheticEvent, state: StateType) {
    event.preventDefault();

    if (!state.name) {
      setMessage(cms.Alle_Felder);
      return;
    }

    if (!state.profilePicture) {
      setMessage(cms.Kein_Bild);
      return;
    }

    const patch = {
      name: state.name,
      ...(state.profilePicture && { profilePicture: state.profilePicture }),
    };

    if (selectedHorse.id) {
      // patch name
      updateHorse({
        userId: user?.id,
        horseId: selectedHorse.id,
        data: patch,
      }).then((result) => {
        // @ts-ignore
        if (result.error) {
          Sentry.captureException(result.error);
          setMessage(cms.Fehler);

          Sentry.captureException(result.error);

          if (result.error.response.status === 413) {
            // image too large
            setMessage("Das Bild ist zu groß.");
          }
          return;
        }
        closeModal();
      });
    } else {
      // create new pferde
      createHorse({
        userId: user?.id,
        data: patch,
      }).then((result) => {
        // @ts-ignore
        if (result.error) {
          Sentry.captureException(result.error);
          setMessage(cms.Fehler);
          console.error(result.error);
          return;
        }
        closeModal();
      });
    }
  }

  useEffect(() => {
    if (fileInput?.current?.files && fileInput?.current?.files[0]) {
      const reader = new FileReader();
      reader.onload = (event) => {
        setState((prevState) => ({
          ...prevState,
          profilePicture: event.target.result,
        }));
      };
      reader.readAsDataURL(fileInput.current.files[0]);
    }
  }, [fileInputChange]);

  function onReset() {
    console.log("reset");
    dispatch(
      setSelectedHorse({
        selectedHorse: {
          id: undefined,
        },
      })
    );
    closeModal();
  }

  if (isLoading || isFetching) {
    return <div>Loading...</div>;
  }

  return (
    <AddHorseForm
      onSubmit={(event: React.SyntheticEvent) => onSubmit(event, state)}
      onReset={onReset}
    >
      <div className={"form-meta"}>
        <h2>{selectedHorse.id ? cms.Titel_Pferd_Bearbeiten : cms.Titel}</h2>
        <p>{cms.Hinweis}</p>
      </div>

      <div className={"input-group form-row"}>
        <label htmlFor={"new-pferde-name"}>{cms.Input_Name}</label>
        <input
          id={"new-pferde-name"}
          name={"name"}
          type={"text"}
          value={state.name}
          className={"input"}
          onChange={(event) => {
            onInputChange(event);
          }}
        />
      </div>
      <div className={"input-group form-row"}>
        <label>{cms.Input_Foto}</label>
        <div className={"picture-input-row"}>
          <ProfilePicWrapper className={"small"}>
            {state.profilePicture ? (
              <img
                id="Profilbild Platzhalter"
                src={state.profilePicture}
                alt="Profilbild Platzhalter"
              />
            ) : (
              <HorseHead
                fill={undefined}
                stroke={undefined}
                strokeLinecap={undefined}
                strokeLinejoin={undefined}
                strokeWidth={undefined}
              />
            )}
          </ProfilePicWrapper>
          <label htmlFor="file-upload" className="input input-file">
            {cms.Input_Foto_Aendern}
          </label>

          <input
            id="file-upload"
            type="file"
            name={"imageSrc"}
            ref={fileInput}
            accept={"image/png, image/jpeg"}
            onChange={() => {
              setFileInputChange(!fileInputChange);
            }}
          />
        </div>
      </div>

      {horseData?.id ? (
        <button
          className={"delete-pferde"}
          onClick={async () => {
            await deleteHorse({ userId: user?.id, horseId: selectedHorse.id });
            closeModal();
          }}
          style={{
            display: selectedHorse.id ? "flex" : "none",
            color: "red",
            marginTop: "1rem",
            backgroundColor: "transparent",
            border: "none",
            alignItems: "center",
            justifyContent: "center",
            gap: "0.5rem",
            cursor: "pointer",
          }}
          type={"button"}
        >
          <TrashIcon
            fill={undefined}
            stroke={undefined}
            strokeLinecap={undefined}
            strokeLineJoin={undefined}
            strokeWidth={undefined}
          />{" "}
          {cms.Pferd_Loeschen}
        </button>
      ) : (
        ""
      )}

      <p className={"message"}>{message}</p>

      <div className={"form-row button-row"}>
        <Button className={"secondary"} type={"reset"}>
          {cms.Abbrechen}
        </Button>
        <Button className={"primary"} type={"submit"}>
          {cms.Speichern}
        </Button>
      </div>
    </AddHorseForm>
  );
}
