import { useSelector } from "react-redux";
import React, { useEffect, useState } from "react";
import { ListViewWrapper } from "./styled";
import Modal from "./AddHorseModal";
import HorseList from "./HorseList";
import { RootState, useAppDispatch } from "../../../redux/store";
import { useHorsesQuery } from "../../../redux/api/horseApi";
import {
  initialAppState,
  setSelectedHorse,
} from "../../../redux/appState/appStateSlice";

/**
 * displays the pferde list or a modal to add a new pferde, depending on state
 * @param cms - relevant content for this component from strapi
 * @returns {JSX.Element}
 */
function ListViewComponent({ cms }: { cms: any }) {
  const user = useSelector((state: RootState) => state.auth.user);

  const dispatch = useAppDispatch();

  const [isModalOpen, setIsModalOpen] = useState(false);
  // if a pferde is selected, the modal is going to show its name and picture
  // otherwise it's creating a new pferde

  const {
    data: horseData,
  } = useHorsesQuery(user?.id as string, {
    refetchOnMountOrArgChange: true,
    skip: !user?.id,
  });

  useEffect(() => {
    console.log(horseData);
  }, [horseData]);

  return (
    <ListViewWrapper>
      <HorseList
        show={!isModalOpen}
        openModal={() => setIsModalOpen(true)}
        isModalOpen={isModalOpen}
        // @ts-ignore
        horses={horseData || []}
        cms={cms}
      />
      <Modal
        closeModal={() => {
          setIsModalOpen(false);
          dispatch(
            setSelectedHorse({ selectedHorse: initialAppState.selectedHorse })
          );
        }}
        cms={cms.Modal}
        // @ts-ignore
        horses={horseData || []}
        isOpen={isModalOpen}
      />
    </ListViewWrapper>
  );
}

export const ListView = ListViewComponent;
